import { v4 } from "uuid";
import { randomNumber } from "../game_manager/utils";

export default class MonsterModel {
  constructor(x, y, gold, spawnerId, key, health, attack, exp, stateTime) {
    this.id = `${spawnerId}-${v4()}`;
    this.spawnerId = spawnerId;
    this.x = x * 2;
    this.y = y * 2;
    this.gold = gold;
    this.key = key;
    this.health = health;
    this.maxHealth = health;
    this.attack = attack;
    this.exp = exp;
    this.stateTime = stateTime;
    this.randomPosition = randomNumber(1, 8);
    this.targetPosition = { x: 0, y: 0 };
    console.log("monstrocriado")
  }

  setTargetPos(position) {
    this.targetPosition = { x: position.x, y: position.y };
  }

  move() {
    const randomPosition = randomNumber(1, 8);
    const distance = 64;
    console.log("pokfespokdsfopk")
    switch (randomPosition) {
      case 1:
        this.x += distance;
        break;
      case 2:
        this.x -= distance;
        break;
      case 3:
        this.y += distance;
        break;
      case 4:
        this.y -= distance;
        break;
      case 5:
        this.x += distance;
        this.y += distance;
        break;
      case 6:
        this.x += distance;
        this.y -= distance;
        break;
      case 7:
        this.x -= distance;
        this.y += distance;
        break;
      case 8:
        this.x -= distance;
        this.y -= distance;
        break;
      default:
        break;
    }
  }

  loseHealth(attack) {
    this.health -= attack;
  }
}
